@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
* {
  margin: 0;
}
.App {
  font-family: "Poppins", sans-serif;
  text-align: center;
}

.navigation {
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  padding: 0.5rem 0rem;
  background-color: #fff;
  color: black;
  z-index: 10;
  box-shadow: 0 2px 2px 2px rgba(9, 9, 9, 0.23);
}
.navigation h2 {
  color: #242f9b;
}
.nav-name {
  padding-left: 5%;
  display: flex;
  flex-direction: column;
  text-align: left;
}
.nav-container {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.brand-name {
  text-decoration: none;
  color: black;
  font-size: 1.3rem;
  margin-left: 1rem;
}
.navigation-menu {
  margin-left: auto;
}

.navigation-menu ul {
  display: flex;
  align-items: center;
  padding: 0;
}
.navigation-menu li {
  list-style-type: none;
  padding-top: 0;
  color: #242f9b;
  font-family: "Poppins";
  margin: 0 1rem;
}

.navigation-menu li:hover {
  cursor: pointer;
}

.hamburger {
  border: 0;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  border-radius: 50%;
  background-color: #283b8b;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  position: absolute;
  top: 50%;
  right: 25px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  display: none;
}
.hamburger:hover {
  background-color: #2642af;
}

@media screen and (max-width: 768px) {
  .hamburger {
    display: block;
  }

  .nav-name p {
    font-size: 12px;
  }
  .navigation-menu ul {
    display: none;
  }
  .navigation-menu ul {
    position: absolute;
    top: 70px;
    left: 0;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 40vh);
    background-color: #dff6ff;
    border-top: 1px solid black;
    transition: 0.3s;
  }
  .navigation-menu li {
    text-align: center;
    margin: 0;
    width: 100%;
    padding: 1.5rem 0;
  }
  .navigation-menu li a {
    color: black;
  }
  .navigation-menu li:hover {
    background-color: #eee;
    cursor: pointer;
  }
  .navigation-menu.expanded ul {
    display: block;
  }
}

.project-container {
  background-color: #dbdffd;
  padding: 3% 3% 5%;
}
.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}
.projects > div {
  width: 35%;
  margin-top: 5%;
  padding-bottom: 2%;
  background-color: #242f9b;
  border-radius: 10px;
  color: #fff;
}
.projects div img {
  width: 95%;
  margin: 3% 0;
  border-radius: 10px;
}
.project-detail a {
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 3px;
  background-color: #fc9918;
  color: #242f9b;
}

.project-detail a:hover {
  background-color: #ff6103;
}
.project-detail h3 {
  padding: 3%;
}

@media (max-width: 786px) {
  .projects {
    display: flex;
    flex-direction: column;
  }

  .projects > div {
    width: 80%;
    padding-bottom: 4%;
  }

  .project-detail a {
    text-decoration: none;
    padding: 2px 5px;
    border-radius: 3px;
    font-size: 12px;
    background-color: #fc9918;
    color: #242f9b;
  }
}

.certification {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 5% 5% 0;
  padding-bottom: 5%;
}

.react-certi img {
  width: 50%;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px,
    rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
  cursor: pointer;
}

.certification a {
  cursor: default;
  width:50%;
}

.certification-container {
  background-color: #242f9b;
  color: #fff;
  padding-top: 4%;
}

@media (max-width: 786px) {
  .certification {
    display: flex;
    flex-direction: column;
  }

  .react-certi img,
  .webdev-certi img {
    width: 80%;
    padding-bottom: 5px;
    border-radius: 10px;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px,
      rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
    cursor: pointer;
  }
}

.contact-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 40%;
  margin: 0 auto;
  padding: 2%;
  border: 1px solid black;
  backdrop-filter: blur(4px) saturate(126%);
  -webkit-backdrop-filter: blur(4px) saturate(126%);
  background-color: rgba(36, 47, 155, 0.78);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.125);
}

.contact-form input,
textarea {
  width: 90%;
  padding: 1%;
}

.contact-form label {
  margin: 3%;
  color: #fff;
}

.contact-form input,
textarea,
button {
  margin: 0 auto;
}

.contact-form button {
  margin-top: 2%;
  padding: 2% 4%;
  backdrop-filter: blur(5px) saturate(180%);
  -webkit-backdrop-filter: blur(5px) saturate(180%);
  background-color: rgba(252, 153, 24, 0.75);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
}

.contact-form button:hover {
  background-color: #fc9918;
}
.contact-container {
  color: #fff;
  padding: 3%;
}

.contact-container h1 {
  padding-bottom: 3%;
}

@media (max-width: 786px) {
  .contact-form {
    width: 90%;
    margin: 0 auto;
  }
}

.profile-info {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 90vh;
  background-color: #242f9b;
  color: #fff;
}

.profile-details h2 {
  overflow: hidden;
  text-align: left;
  font-size: 1.5rem;
  border-right: 0.15em solid #dff6ff;
  white-space: nowrap;
  margin: 5% auto 5%;
  letter-spacing: 0.15em;
  -webkit-animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
          animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
}

.profile-details div a {
  margin: 2% 5%;
  font-size: 35px;
  color: #dbdffd;
}

.profile-details > a button {
  text-decoration: none;
  background-color: #dff6ff;
  border-radius: 5px;
  font-size: 15px;
  font-weight: 600;
  color: #242f9b;
  padding: 3% 5%;
  margin: 5%;
}

.profile-details > a button:hover {
  cursor: pointer;
  background-color: #fff;
}
.profile-details h1 {
  text-align: left;
}

.profile-details h1:nth-child(1) {
  font-size: 2.5rem;
  margin-bottom: 0;
}

.profile-details h1:nth-child(2) {
  font-size: 3.5rem;
  color: #ff6103;
}

.profile-image {
  background-color: #9ba3eb;
  width: 20%;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}

@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #dff6ff;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #dff6ff;
  }
}

@media (max-width: 700px) {
  .profile-info {
    display: flex;
    flex-direction: column-reverse;
  }
  .profile-details h2 {
    overflow: hidden;
    text-align: left;
    font-size: 1em;
    border-right: 0.15em solid #dff6ff;
    white-space: nowrap;
    margin: 5% auto 5%;
    -webkit-animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
            animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
  }
  .profile-details h1 {
    text-align: center;
  }

  .profile-details h1:nth-child(1) {
    font-size: 1rem;
    margin-bottom: 0;
  }

  .profile-details h1:nth-child(2) {
    font-size: 2rem;
    color: #ff6103;
  }

  .profile-image {
    background-color: #9ba3eb;
    width: 20%;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  }

  .profile-image {
    background-color: #9ba3eb;
    width: 50%;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  }

  .profile-details div a {
    margin: 2% 5%;
    font-size: 25px;
    color: #dbdffd;
  }

  @-webkit-keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }

  @keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }

  /* The typewriter cursor effect */
  @-webkit-keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: #dff6ff;
    }
  }
  @keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: #dff6ff;
    }
  }
}

.about-me {
  text-align: center;
  margin: 3% auto 5%;
  width: 80%;
  /* padding: 10% 0; */
  background-color: #dbdffd;
  border-radius: 10px;
}

.aboutme-container > h1 {
  padding-top: 3%;
}
.aboutme-tab h3 {
  word-spacing: 0.5rem;
  line-height: 2.5rem;
  text-align: justify;
}

.about-nav th {
  width: 20%;
}
.aboutme-tab {
  text-align: left;
  margin: 0 auto;
  width: 70%;
}

.aboutme-tab h4,
li {
  padding-top: 2%;
}

.aboutme-tab li {
  list-style: none;
  font-weight: bold;
}
.aboutme-tab li::before {
  content: "📍";
  margin-right: 5px;
}
.about-content {
  padding: 5% 10%;
}

.about-me button {
  border: none;
  padding: 5%;
  width: 100%;
  color: #242f9b;
  font-weight: bold;
  font-size: 100%;
  font-family: "Poppins";
  background-color: #dbdffd;
  /* border-bottom: 1px solid #fff; */
}

.education {
  margin-bottom: 3%;
}

.education h3 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.education h3 span {
  background-color: #242f9b;
  color: #fff;
  padding: 3px 10px;
  border-radius: 5px;
}

.education h4 {
  padding: 0;
}

.education h4 span {
  color: #242f9b;
}


.skill {
  text-align: left;
  display: flex;
  justify-content: space-evenly;
}

.skill ul li {
  margin-bottom: 15%;
}

.progress {
  width: 200px;
  height: 10px;
  border-radius: 5px;
  border: 1px solid #242f9b;
  position: relative;
}
.progress1:after {
  content: "\A";
  position: absolute;
  background: #242f9b;
  top: 0;
  bottom: 0;
  left: 0;
  width: 80%;
  -webkit-animation: filler 2s ease-in-out;
  animation: filler 2s ease-in-out;
}

.progress2:after {
  content: "\A";
  position: absolute;
  background: #242f9b;
  top: 0;
  bottom: 0;
  left: 0;
  width: 80%;
  -webkit-animation: filler 2s ease-in-out;
  animation: filler 2s ease-in-out;
}
.progress3:after {
  content: "\A";
  position: absolute;
  background: #242f9b;
  top: 0;
  bottom: 0;
  left: 0;
  width: 75%;
  -webkit-animation: filler 2s ease-in-out;
  animation: filler 2s ease-in-out;
}
.progress4:after {
  content: "\A";
  position: absolute;
  background: #242f9b;
  top: 0;
  bottom: 0;
  left: 0;
  width: 70%;
  -webkit-animation: filler 2s ease-in-out;
  animation: filler 2s ease-in-out;
}
.progress5:after {
  content: "\A";
  position: absolute;
  background: #242f9b;
  top: 0;
  bottom: 0;
  left: 0;
  width: 75%;
  -webkit-animation: filler 2s ease-in-out;
  animation: filler 2s ease-in-out;
}

@-webkit-keyframes filler {
  0% {
    width: 0;
  }
}
@keyframes filler {
  0% {
    width: 0;
  }
}

@media (max-width: 786px) {
